// 11. Button
$button-font-family: inherit;
$button-padding: 0.85em 1em;
$button-margin: 0 0 $global-margin 0;
$button-fill: solid;
$button-background: transparent;
$button-background-hover: scale-color($button-background, $lightness: -15%);
